import React, {useState} from 'react';
import './App.css';
import {Routes, Route} from "react-router-dom"
import Home from "./home/Home";
import Post from "./home/Post";
import Cookies from 'universal-cookie';
import Login from "./login/Login";
import RouteGuard from "./login/RouteGuard";
import Nav from "./Nav";
import Gallery from "./Gallery";

const cookies = new Cookies();

function handleAdmin() {
    cookies.set('logged', true);
    console.log(cookies.get('logged'));
}


function App() {
    const [lang, setLang] = useState(cookies.get("lang"));

    function setNL() {
        cookies.set('lang', "NL");
        console.log(cookies.get("lang"))
        setLang(cookies.get("lang"))

    }

    function setEN() {
        cookies.set('lang', "EN");
        console.log(cookies.get("lang"))
        setLang(cookies.get("lang"))


    }

    function setES() {
        cookies.set('lang', "ES");
        console.log(cookies.get("lang"))
        setLang(cookies.get("lang"))

    }

    return (
        <div>
            <Nav setNL={setNL} setEN={setEN} setES={setES}/>
            <Routes>
                <Route path="/" element={<Home lang={lang}/>}/>
                <Route path="/post/:id" element={<Post lang={lang}/>}/>
                <Route path="/login" element={<Login handleAdmin={handleAdmin} />}/>
                <Route
                    path="/master"
                    element={
                        <RouteGuard />
                    }
                />
                <Route path="/gallery" element={<Gallery/>}/>

            </Routes>

        </div>

    );
}

export default App;
