import React, {useEffect, useState} from 'react';
import {
    Typography,
    Grid,
    Box,
    Button,
    List,
    ListItem,
    Card,
    CardContent,
    TextField,
    createTheme,
    ThemeProvider,
    IconButton,
    Stack,
    MenuItem,

} from "@mui/material";
import {DatePicker} from '@mui/x-date-pickers/DatePicker';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';

import Axios from "axios";
import {styled} from "@mui/styles";


const CreateReis = () => {
    const [name, setName] = useState("")
    const [datum, setDatum] = useState<Date | null>(null)
    const [foto, setFoto] = useState<any | null>("")
    const [headers, setHeaders] = useState<any | null>([])
    const [reisID, setReisID] = useState(0)
    const StyledButton = styled(Button)({
        "&:hover": {
            backgroundColor: "rgba(232, 213, 181, 1) !important"
        }
    })


    const submit = () => {
        if (!(name == "" || datum == null)) {
            const data = new FormData()
            console.log(foto)
            data.append("file", foto[0])
            data.append("upload_preset", "ggt13hie")
            fetch("https://api.cloudinary.com/v1_1/vlogblog/image/upload", {
                method: "post",
                body: data
            }).then(
                resp => resp.json())
                .then(data => {
                    Axios.post("https://bnbserver.herokuapp.com/api/createreis", {name: name, date: datum, foto: data.url}
                    ).then((res) => {
                        console.log(res.data.insertId)
                        setReisID(res.data.insertId)
                    })
                }).catch(err => console.log(err)
            )







        }

    }


    useEffect(() => {

        const arrFotos = Array.from(headers)
        arrFotos.map((foto: any) => {
            const data = new FormData()
            data.append("file", foto)
            data.append("upload_preset", "ggt13hie")
            console.log(foto.name)
            fetch(`https://api.cloudinary.com/v1_1/vlogblog/image/upload`, {
                method: "post",
                body: data
            }).then(
                resp => resp.json())
                .then(data => {
                    console.log(reisID)
                    Axios.post("https://bnbserver.herokuapp.com/api/uploadheader", {foto: data.secure_url, reisID: reisID})
                }).catch(err => console.log(err)
            )
        })

        setName("")
        setDatum(null)
        setFoto("")
        setHeaders([])

    }, [reisID])


    return (
        <div>
            <Card sx={{
                width: "45vw",
                display: "flex",
                mt: "24px",
                mb: "24px",
                alignItems: "center",
                justifyContent: "space-around",
                height: "520px",
                background: "#65A0A1",
            }}>
                <Stack spacing={2} sx={{p: "1em", width: "100%"}}>
                    <TextField

                        fullWidth
                        variant="standard"
                        inputProps={{readOnly: true, style: {textAlign: "center"}}}
                        value="Maak nieuwe reis"


                    />
                    <TextField required label="naam" value={name}
                               onChange={e => setName(e.target.value)}

                    />
                    <LocalizationProvider dateAdapter={AdapterDateFns}>
                    <DatePicker label={"datum"} value={datum} onChange={(date) => {
                        setDatum(date)


                    }}
                                renderInput={(params) => <TextField {...params} />}/>
                    </LocalizationProvider>
                    <StyledButton
                        variant="contained"
                        sx={{minHeight: "75px", backgroundColor: "rgba(252,233,196,0.7)",color:"#000"}}
                    >
                        <label htmlFor="foto" style={{marginLeft: "33%", marginRight: "auto", width: "100%"}}>kies
                            foto</label>
                        <input
                            style={{visibility: "hidden", width: "100%"}}
                            id="foto"
                            type="file" onChange={(e) => setFoto(e.target.files)}
                        />
                    </StyledButton>
                    <StyledButton
                        variant="contained"
                        sx={{minHeight: "75px", backgroundColor: "rgba(252,233,196,0.7)",color:"#000"}}
                    >
                        <label htmlFor="headers" style={{marginLeft: "33%", marginRight: "auto", width: "100%"}}>kies
                            header foto's</label>
                        <input
                            multiple
                            style={{visibility: "hidden", width: "100%"}}
                            id="headers"
                            type="file" onChange={(e) => setHeaders(e.target.files)}
                        />
                    </StyledButton>
                    <StyledButton variant="contained"  sx={{minHeight: "75px", backgroundColor: "rgba(252,233,196,0.4)",color:"#000"}} onClick={submit}
                    >
                        Maak nieuw pakket
                    </StyledButton>
                </Stack>
            </Card>
        </div>
    );
};

export default CreateReis;
