import React from 'react';
import CreateReis from "./master/CreateReis";
import CreatePost from "./master/CreatePost";
import {Card, Stack} from "@mui/material";
import UpdateQuote from "./master/UpdateQuote";


const Master = () => {
    return (
        <div style={{marginTop: "48px"}}>
            <Stack spacing={2} sx={{p: "1em", width: "100%",}} direction={"row"}>
            <CreateReis/>
            <CreatePost/>

            </Stack>
            <UpdateQuote />
        </div>
    );
};

export default Master;
