import React, {useEffect, useState} from 'react';
import {Box, Typography} from "@mui/material";
import LinkExample from "./LinkExample";
import {Link} from "react-router-dom";
import Axios, {AxiosResponse} from "axios";

type props = {
    lang: string,
}

const Home = (props: props) => {
    const [quote, setQuote] = useState<any>([])

    function getData() {
        Axios.get('https://bnbserver.herokuapp.com/api/getquote').then((response: AxiosResponse) => {
            setQuote(response.data)
        })
    }


    useEffect(() => {
        getData()
    }, [])
    return (
        <>
            <Box className="navimg"></Box>

            <Box sx={{background: "#2c2c56", mt: "1px", p: "3em"}}>
                {quote.map((q: any) => {
                    return (
                        <>
                            <Typography variant="h1" className="quote"> {
                                props.lang == "NL" ? <>{q.titelNL}</> : props.lang == "EN" ?
                                    <>{q.titelEN}</> :
                                    <>{q.titelES}</>

                            }</Typography>
                            <p className="text">{props.lang == "NL" ? <>{q.tekstNL}</> : props.lang == "EN" ?
                                <>{q.tekstEN}</> :
                                <>{q.tekstES}</>}</p>
                        </>

                    )
                })}



                    </Box>
                    <Box>
                    <LinkExample/>
                    </Box>

                    </>
                    );
                };

export default Home;
