import React, {useEffect, useState} from 'react';
import {useParams} from 'react-router-dom';
import {Box, Grid, Stack, Typography} from "@mui/material";
import Axios, {AxiosResponse} from "axios";
import Headers from "../header/Headers"
import Cookies from 'universal-cookie';
import {Cloudinary} from "@cloudinary/url-gen";

const cookies = new Cookies();

type props = {
    lang: string,
}
const Post = (props: props) => {
        let {id} = useParams();
        const [post, setPost] = useState([])
        const [content, setContent] = useState([])
        const [images, setImages] = useState<any>([])
        const [cloudImages, setCloudImages] = useState<any>([])
        const cld = new Cloudinary({
            cloud: {
                cloudName: 'vlogblog',
                apiKey: "856378898956536",
                apiSecret: "ZVTcys31UlGTds8WtYANLJyJ8RM",
            }
        });

        function getData() {
            Axios.get(`https://bnbserver.herokuapp.com/api/getheader/${id}`).then((response: AxiosResponse) => {
                setPost(response.data)
            })
            Axios.get(`https://bnbserver.herokuapp.com/api/getposts/${id}`).then((response: AxiosResponse) => {
                setContent(response.data)
            })
            Axios.get(`https://bnbserver.herokuapp.com/api/getimages`).then((response: AxiosResponse) => {
                setImages(response.data)
            })

        }


        useEffect(() => {
            getData()
        }, [])

        return (
            <>
                <Headers id={id}/>
                <Box sx={{background: "#2c2c56", mt: "1px", p: "3em"}}>
                    {post.map((result: any) => {
                        const date = result.datum.slice(0, 10)
                        return (
                            <>
                                <Typography variant="h1" className="quote">{result.naam}</Typography>
                                <Typography variant="body2" className="text">{date}</Typography>
                            </>
                        )
                    })}

                </Box>


                {


                    content.map((result: any) => {

                        return (
                            <>
                                <Box sx={{background: "#525296", mt: "24px", p: "3em", borderRadius: "16px", mb: "24px"}}>
                                    {
                                        props.lang == "NL" ? <Typography variant="body2"
                                                                         className="content">{result.tekstNL}</Typography> : props.lang == "EN" ?
                                            <Typography variant="body2" className="content">{result.tekstEN}</Typography> :
                                            <Typography variant="body2" className="content">{result.tekstES}</Typography>

                                    }
                                </Box>
                                <Grid container>
                                    {
                                        images.map((foto: any) => {

                                            if (foto.postID == result.postID) {
                                                if (foto.foto.substring(foto.foto.length - 3) != "mkv" && foto.foto.substring(foto.foto.length - 3) != "mp4" )
                                                {return (
                                                    <Grid item xs={12} md={6} sx={{p: "10px"}}>
                                                        <img src={foto.foto} alt="foto" style={{
                                                            width: "100%",
                                                            height: "auto",
                                                            maxHeight: "80vh",
                                                            float: "left",
                                                        }}/>
                                                    </Grid>
                                                )}
                                                else{
                                                    return (
                                                        <Grid item xs={12} md={6} sx={{p: "10px"}}>
                                                            <video src={foto.foto} controls style={{
                                                                width: "100%",
                                                                height: "auto",
                                                                maxHeight: "80vh",
                                                                float: "left",
                                                            }}/>
                                                        </Grid>
                                                    )
                                                }
                                            }


                                        })
                                    }
                                </Grid>

                            </>

                        )
                    })
                }


            </>
        );
    }
;

export default Post;
