import React, {useEffect, useState} from 'react';
import Axios, {AxiosResponse} from "axios";
import HeaderBig from "./HeaderBig"
import {Cloudinary} from "@cloudinary/url-gen";
import {Grid, Stack} from "@mui/material";

type props = {
    id: any,
}

const Headers = (props: props) => {

    const [small, setSmall] = useState(0)
    const [headers, setHeaders] = useState<any>([])

    const cld = new Cloudinary({
        cloud: {
            cloudName: 'vlogblog',
            apiKey: "856378898956536",
            apiSecret: "ZVTcys31UlGTds8WtYANLJyJ8RM",
        }
    });

    function getData() {
        Axios.get(`https://bnbserver.herokuapp.com/api/getpostheaders/${props.id}`).then((response: AxiosResponse) => {
            setHeaders(response.data)
        })


    }

    useEffect(() => {
        getData()
    }, [])

    useEffect(() => {
        function handleResize() {
            if (window.innerWidth < 900) {
                setSmall(1)
            } else {
                setSmall(0)
            }
        }

        window.addEventListener('resize', handleResize)
    })


    return (
        <>
            <Stack direction={"row"} sx={{display: "flex", justifyContent: "space-between", pt: "5em"}}>
            {
                headers.map((foto: any) => {
                    return (
                            <img src={foto.headerIMG} alt="foto" className="image-margin"/>
                    )
                })
            }
            </Stack>
        </>
    )


};

export default Headers;
