import React, {useState} from 'react';
import {styled} from "@mui/styles";
import {Button, Card, Stack, TextField} from "@mui/material";
import Axios from "axios";


const UpdateQuote = () => {

    const [tekstNL, setTekstNL] = useState("")
    const [tekstEN, setTekstEN] = useState("")
    const [tekstES, setTekstES] = useState("")


    const [titelNL, setTitelNL] = useState("")
    const [titelEN, setTitelEN] = useState("")
    const [titelES, setTitelES] = useState("")

    const StyledButton = styled(Button)({
        "&:hover": {
            backgroundColor: "rgba(232, 213, 181, 1) !important"
        }
    })

    const submit = () => {

        if (!(titelNL == ""|| titelES || titelEN || titelNL == "" || tekstES == "" || tekstEN)) {
            Axios.post("https://bnbserver.herokuapp.com/api/setquote", {
                    tekstNL: tekstNL,
                    tekstEN: tekstEN,
                    tekstES: tekstES,
                    titelEN: titelEN,
                    titelNL: titelNL,
                    titelES: titelES,
                }
            )
        }

    }

    return (
        <div>
            <Card sx={{
                width: "45vw",
                display: "flex",
                mt: "24px",
                mb: "24px",
                ml: "24px",
                alignItems: "center",
                justifyContent: "space-around",
                height: "800px",
                background: "#65A0A1",
            }}>
                <Stack spacing={2} sx={{p: "1em", width: "100%"}}>
            <TextField required label="titelNL" value={titelNL}
                       onChange={e => setTitelNL(e.target.value)}
            />
            <TextField required label="titelEN" value={titelEN}
                       onChange={e => setTitelEN(e.target.value)}
            />
            <TextField required label="titelES" value={titelES}
                       onChange={e => setTitelES(e.target.value)}
            />

            <TextField required label="tekst nl" value={tekstNL}
                       onChange={e => setTekstNL(e.target.value)}
                       multiline rows="3"


            />
            <TextField required label="tekst en" value={tekstEN}
                       onChange={e => setTekstEN(e.target.value)}
                       multiline rows="3"


            />
            <TextField required label="tekst es" value={tekstES}
                       onChange={e => setTekstES(e.target.value)}
                       multiline rows="3"


            />

            <StyledButton variant="contained"
                          sx={{minHeight: "75px", backgroundColor: "rgba(252,233,196,0.4)",color:"#000"}}
                          onClick={submit}>
                Maak post
            </StyledButton>
                </Stack>
            </Card>
        </div>
    );
};

export default UpdateQuote;
