import React, {useEffect, useState} from 'react';
import {
    AppBar,
    Grid,
    Toolbar,
    Typography,
    IconButton,
    Drawer,
    Box,
    List,
    ListItemButton,
    ListItemText, Select, SelectChangeEvent, MenuItem
} from "@mui/material"
import {Link} from "react-router-dom";
import MenuIcon from '@mui/icons-material/Menu';

import Cookies from 'universal-cookie';

const cookies = new Cookies();

type props = {
    setNL:any,
    setEN:any,
    setES:any,
}

let navlinks = {
    display: "flex",
    justifyContent: "space-around",
    alignItems: "center",
    width: "100%",
    position: "sticky",
}

let navlinksSmall = {
    color: "white",
    fontSize: "20px",
    "&:hover": {
        color: "#e2b402",
    },
}


let link = {
    borderRadius: 0,
    textDecoration: "none",
    color: "white",
    fontSize: "20px",
    display: {xs: 'none', sm: 'block',},
}


const Nav = (props:props) => {
    const [open, setState] = useState(false);
    const [lang, setLang] = useState(cookies.get("lang"));
    const toggleDrawer = (open: boolean) => (event: any) => {
        if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
            return;
        }
        setState(open);
    };

    useEffect(()=>{
        if( lang =="NL"){
            props.setNL()

        }else if (lang =="ES"){
            props.setES()
        }else{
            props.setEN()
        }

    }, [lang])

    return (
        <Box>
            <AppBar sx={{width: "100%", backgroundColor: "#131212"}}>
                <Toolbar>
                    <Grid container>
                        <Grid item xs={11} sx={navlinks}>
                            <IconButton
                                aria-label="open drawer"
                                onClick={toggleDrawer(true)}
                                sx={{display: {xs: 'block', sm: 'none',}, m: "auto"}}>
                                <MenuIcon sx={{color: "white"}}/>
                            </IconButton>
                            <Drawer
                                anchor="top"
                                variant="temporary"
                                open={open}
                                onClose={toggleDrawer(false)}

                            >
                                <Box sx={{backgroundColor: "#272727", color: "white"}}>
                                    <Link to="/">
                                        <Typography onClick={toggleDrawer(false)} sx={navlinksSmall}>Home</Typography>
                                    </Link>
                                    <Link to="/contact">
                                        <Typography onClick={toggleDrawer(false)}
                                                    sx={navlinksSmall}>contact</Typography>
                                    </Link>
                                    <Link to="/gallery">
                                        <Typography onClick={toggleDrawer(false)} sx={navlinksSmall}>over
                                            ons</Typography></Link>
                                </Box>
                            </Drawer>


                            <Link to="/">
                                <Typography sx={link}>Home</Typography>
                            </Link>
                            <Link to="/about">
                                <Typography sx={link}>about</Typography>
                            </Link>
                            <Link to="/gallery">
                                <Typography sx={link}>gallery</Typography>
                            </Link>


                        </Grid>
                        {/* <Grid item xs={1} sx={{pt:"10px"}}>
                            <Grid container>
                                <Grid item xs={6} sx={{mb:"10px"}}><a href="https://www.facebook.com/verheyen.kelly"><FacebookIcon
                                    sx={{color: "white"}}/></a></Grid>
                                <Grid item xs={6}><a href="https://www.instagram.com/verheyen.kelly/"><InstagramIcon
                                    sx={{color: "white"}}/></a>
                                </Grid>
                                <Grid item xs={6}> <a href="https://www.instagram.com/verheyen.kelly/"><EmailIcon
                                    sx={{color: "white"}}/></a>
                                </Grid>
                                <Grid item xs={6}> <a href="https://www.instagram.com/verheyen.kelly/"><CameraAltIcon
                                    sx={{color: "white"}}/></a>
                                </Grid>
                            </Grid>
                        </Grid>*/}

                        <Select
                            value={lang}
                            label="lang"
                            onChange={(event:any) =>{
                                setLang(event.target.value)
                            }}
                            className="lang-select"
                            sx={{
                                color: "white", border: "1px solid white",
                            }}

                        >
                            <MenuItem value="NL">NL</MenuItem>
                            <MenuItem value="EN">EN</MenuItem>
                            <MenuItem value="ES">ES</MenuItem>
                        </Select>


                    </Grid>


                </Toolbar>

            </AppBar>
        </Box>
    );
};

export default Nav;
