import React, {useEffect, useState} from 'react';
import {Box, Grid} from "@mui/material";
import Axios, {AxiosResponse} from "axios";

const Gallery = () => {
    const [images, setImages] = useState<any>([])
    function getData() {
        Axios.get(`http://localhost:3001/api/getimages`).then((response: AxiosResponse) => {
            setImages(response.data)
        })

    }


    useEffect(() => {
        getData()
    }, [])

    return (
        <Box sx={{mt: "80px"}}>
            <Grid container>
                {
                    images.map((foto: any) => {
                        if (foto.foto.substring(foto.foto.length - 3) != "mkv" && foto.foto.substring(foto.foto.length - 3) != "mp4") {
                            return (
                                <Grid item xs={12} md={6} sx={{p: "10px"}}>
                                    <img src={foto.foto} alt="foto" style={{
                                        width: "100%",
                                        height: "auto",
                                        maxHeight: "80vh",
                                        float: "left",
                                    }}/>
                                </Grid>
                            )
                        } else {
                            return (
                                <Grid item xs={12} md={6} sx={{p: "10px"}}>
                                    <video src={foto.foto} controls style={{
                                        width: "100%",
                                        height: "auto",
                                        maxHeight: "80vh",
                                        float: "left",
                                    }}/>
                                </Grid>
                            )
                        }


                    })
                }
            </Grid>
        </Box>
    );
};

export default Gallery;
