import React, {useEffect, useState} from 'react';
import Axios, {AxiosResponse} from "axios";
import {Button, Card, MenuItem, Select, Stack, TextField} from "@mui/material";
import {styled} from "@mui/styles";
import {Cloudinary} from "@cloudinary/url-gen";


const CreatePost = () => {

    const [tekstNL, setTekstNL] = useState("")
    const [tekstEN, setTekstEN] = useState("")
    const [tekstES, setTekstES] = useState("")
    const [reizen, setReizen] = useState([])
    const [reis, setReis] = useState(0)
    const [fotos, setFotos] = useState<any | null>([])
    const [post, setPost] = useState(0)

    const StyledButton = styled(Button)({
        "&:hover": {
            backgroundColor: "rgba(232, 213, 181, 1) !important"
        }
    })


    const submit = () => {

        if (!(tekstNL == "" || tekstEN == "" || tekstES == "")) {
            Axios.post("https://bnbserver.herokuapp.com/api/createpost", {
                    tekstNL: tekstNL,
                    tekstEN: tekstEN,
                    tekstES: tekstES,
                    reisID: reis
                }
            ).then((res) => {
                setPost(res.data.insertId)
            })


        }

    }

    function getData() {
        Axios.get('https://bnbserver.herokuapp.com/api/getheaders').then((response: AxiosResponse) => {
            setReizen(response.data)
        })
    }

    useEffect(() => {
        getData()
    }, [])

    useEffect(() =>{
        const arrFotos = Array.from(fotos)
        arrFotos.map((foto: any) => {
            const data = new FormData()
            data.append("file", foto)
            data.append("folder", reis.toString())
            data.append("upload_preset", "ggt13hie")
            console.log(foto.name)
            fetch(`https://api.cloudinary.com/v1_1/vlogblog/upload`, {
                method: "post",
                body: data
            }).then(
                resp => resp.json())
                .then(data => {
                    console.log(post)
                    Axios.post("https://bnbserver.herokuapp.com/api/uploadfoto", {foto: data.secure_url, postID: post})
                }).catch(err => console.log(err)
            )
        })
        setTekstNL("")
        setTekstES("")
        setTekstEN("")
        setFotos([])

    },[post])

    return (
        <div>
            <Card sx={{
                width: "45vw",
                display: "flex",
                mt: "24px",
                mb: "24px",
                alignItems: "center",
                justifyContent: "space-around",
                height: "680px",
                background: "#65A0A1",

            }}>

                <Stack spacing={2} sx={{p: "1em", width: "100%",}}>
                    <TextField
                        fullWidth
                        variant="standard"
                        inputProps={{readOnly: true, style: {textAlign: "center"}}}
                        value="maak post"

                    />
                    <Select
                        value={reis}
                        label="selecteer reis"
                        onChange={(e) => {
                            setReis(e.target.value as number)

                        }}
                    >
                        {reizen.map((reis: any) => {
                            return (
                                <MenuItem value={reis.reisID}>{reis.naam}</MenuItem>
                            )
                        })}

                    </Select>
                    <TextField required label="tekst nl" value={tekstNL}
                               onChange={e => setTekstNL(e.target.value)}
                               multiline rows="3"


                    />
                    <TextField required label="tekst en" value={tekstEN}
                               onChange={e => setTekstEN(e.target.value)}
                               multiline rows="3"


                    />
                    <TextField required label="tekst es" value={tekstES}
                               onChange={e => setTekstES(e.target.value)}
                               multiline rows="3"


                    />

                    <StyledButton
                        variant="contained"
                        sx={{minHeight: "75px", backgroundColor: "rgba(252,233,196,0.7)",color:"#000"}}
                    >
                        <label htmlFor="fotos" style={{marginLeft: "33%", marginRight: "auto", width: "100%"}}>kies
                            foto's</label>
                        <input
                            id="fotos" type="file" multiple onChange={(e) => setFotos(e.target.files)}
                            style={{visibility: "hidden", width: "100%"}}
                        />
                    </StyledButton>
                    <StyledButton variant="contained"
                                  sx={{minHeight: "75px", backgroundColor: "rgba(252,233,196,0.4)",color:"#000"}}
                                  onClick={submit}>
                        Maak post
                    </StyledButton>
                </Stack>

            </Card>
        </div>
    );
};

export default CreatePost;
