import React, {useEffect, useState} from 'react';
import {Box, Button, Grid, Typography} from "@mui/material";
import Axios, {AxiosResponse} from "axios";
import {Cloudinary} from "@cloudinary/url-gen";


let textstyle = {
    textAlign: "center",
    color: "white",
}


const LinkExample = () => {
    const [headers, setHeaders] = useState<any>([]);
    const [ID, setID] = useState(0);
    const [images, setImages] = useState<any>([])
    const cld = new Cloudinary({
        cloud: {
            cloudName: 'vlogblog',
            apiKey: "856378898956536",
            apiSecret:"ZVTcys31UlGTds8WtYANLJyJ8RM",
        }
    });

    function getData() {
        Axios.get('https://bnbserver.herokuapp.com/api/getheaders').then((response: AxiosResponse) => {
            setHeaders(response.data)
        })
        let tempImages: any = []
        headers.map((h:any) => {
            tempImages.push(cld.image(h.foto).toURL())
            setImages(tempImages)
        })


    }


    useEffect(() => {
        getData()
    }, [])



    return (
        <Grid container sx={{mt: "5px"}}>
            {
                headers.map((header: any) => {

                    return (
                        <Grid item xs={12} sm={6} sx={{p: "10px"}}>
                                    <a href={`/#/post/${ID}`} onClick={()=>setID(header.reisID)} >
                                            <Box className="test"
                                                 style={{
                                                     width: "100%",
                                                     height:"300px",
                                                     paddingLeft:"1em",
                                                     display:"flex",
                                                     alignItems:"flex-end",
                                                     justifyContent:"center",
                                                 }}
                                            >
                                                <img src={header.foto} className="header-image"/>

                                            </Box>

                                    </a>
                        </Grid>
                    )
                })
            }
        </Grid>
    )
        ;
};

export default LinkExample;
